import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const Main = (resolve) => {
  import('@/main/main').then((module) => {
    resolve(module)
  })
}

// const Main2 = (resolve) => {
//   import('@/main/main2').then((module) => {
//     resolve(module)
//   })
// }

// 首页
const HelloWorld = (resolve) => {
  import('@/components/HelloWorld').then((module) => {
    resolve(module)
  })
}

// 提示注册页面
const PromptRegistration = (resolve) => {
  import('@/views/registration/promptRegistration').then((module) => {
    resolve(module)
  })
}

// 注册页面
const register = (resolve) => {
  import('@/views/registration/register').then((module) => {
    resolve(module)
  })
}

// 医院介绍
const hospitalInfo = (resolve) => {
  import('@/views/hospitalInfo/hospitalInfo').then((module) => {
    resolve(module)
  })
}

// 专家介绍
const expertIntroduction = (resolve) => {
  import('@/views/expertIntroduction/expertIntroduction').then((module) => {
    resolve(module)
  })
}

// 在线预约
const doctorVerb = (resolve) => {
  import('@/views/doctorVerb/doctorVerb').then((module) => {
    resolve(module)
  })
}

// 预约成功
const verbSuccess = (resolve) => {
  import('@/views/verbSuccess/verbSuccess').then((module) => {
    resolve(module)
  })
}

// 核对订单
const checkOrder = (resolve) => {
  import('@/views/checkOrder/checkOrder').then((module) => {
    resolve(module)
  })
}

// 预约取消
const cancelVerb = (resolve) => {
  import('@/views/cancelVerb/cancelVerb').then((module) => {
    resolve(module)
  })
}

// 所有预约
const allOrder = (resolve) => {
  import('@/views/allOrder/allOrder').then((module) => {
    resolve(module)
  })
}

// 待就诊详情页
const orderReady = (resolve) => {
  import('@/views/orderReady/orderReady').then((module) => {
    resolve(module)
  })
}

// 已就诊详情页
const orderFinish = (resolve) => {
  import('@/views/orderFinish/orderFinish').then((module) => {
    resolve(module)
  })
}

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    // 提示注册页面
    {
      path: '/PromptRegistration',
      name: 'PromptRegistration',
      component: PromptRegistration
    },
    // 注册页面
    {
      path: '/register',
      name: 'register',
      component: register
    },
    // 医院介绍
    {
      path: '/hospitalInfo',
      name: 'hospitalInfo',
      component: hospitalInfo
    },
    // 专家介绍
    {
      path: '/',
      name: 'expertIntroduction',
      component: Main,
      children: [
        {
          path: 'expertIntroduction',
          component: expertIntroduction
        }
      ]
    },
    // 在线预约
    {
      path: '/',
      name: 'doctorVerb',
      component: Main,
      children: [
        {
          path: 'doctorVerb',
          component: doctorVerb
        }
      ]
    },
    // 核对订单
    {
      path: '/',
      name: ' checkOrder',
      component: Main,
      children: [
        {
          path: 'checkOrder',
          component: checkOrder
        }
      ]
    },
    // 预约成功
    {
      path: '/',
      name: ' verbSuccess',
      component: Main,
      children: [
        {
          path: 'verbSuccess',
          component: verbSuccess
        }
      ]
    },
    // 预约取消
    {
      path: '/',
      name: 'cancelVerb',
      component: Main,
      children: [
        {
          path: 'cancelVerb',
          component: cancelVerb
        }
      ]
    },
    // 预约取消
    {
      path: '/',
      name: 'allOrder',
      component: Main,
      children: [
        {
          path: 'allOrder',
          component: allOrder
        }
      ]
    },
    // 待就诊
    {
      path: '/',
      name: 'orderReady',
      component: Main,
      children: [
        {
          path: 'orderReady',
          component: orderReady
        }
      ]
    },
    // 已就诊
    {
      path: '/',
      name: 'orderFinish',
      component: Main,
      children: [
        {
          path: 'orderFinish',
          component: orderFinish
        }
      ]
    }
  ]
})



